.Toolbar {
    height: 300px; 
    width: 100%;
    display: flex; 
    flex-direction: row;
    align-items: center;
    background-color: #00e6e6;    
}

.Toolbar ul {
    list-style: none; 
    margin: 0; 
    padding: 0; 
}

.Toolbar li {
    display: block; 
    margin: 20px;    
}

.Toolbar a{
    text-decoration: none;
    color: black; 
    font-size: 42px; 
}

.Toolbar a:hover, 
.Toolbar a:active{
    color: #999999; 
}

@media(min-width: 600px){
    .Toolbar {
        height: 120px; 
        width: 100%;
        display: flex; 
        flex-direction: row;
        justify-content: space-around;
        align-items: center;
        text-align: center;

        
    }
       
    .Toolbar li {
        display: inline-block; 
        margin: 40px;       
    }

    .Toolbar a{
        text-decoration: none;
        color: black; 
        font-size: 25px; 
    }

}