.AreYouSureBlock{
    position: fixed; 
    z-index: 500; 
    background-color: #f2f2f2;
    width: 60%; 
    border: 1px solid black; 
    box-shadow: 1px 1px 1px black;
    padding: 16px; 
    left: 15%; 
    top:30%; 
    box-sizing: border-box; 
    transition: all 0.3s ease-out; 
}

@media(min-width: 600px){
    .AreYouSureBlock{
        width: 500px; 
        left:calc(50% - 250px); 
    }
}